.end {
  background-color: $blue;

  &__container {
    height: 100%;
  }

  &__coffee {
    position: absolute;
    left: 5.3333%;
    bottom: 87%;
    z-index: 5;

    @include md-down {
      @media screen and (max-height: 667px) {
        bottom: 87%;
      }

      @media screen and (min-height: 668px) {
        bottom: 72.2906%;
      }
    }

    @include md-down {
      width: 41.0666%;
    }

    @include md-up {
      top: -22.7777%;
      left: auto;
      right: 51.5625%;
    }
  }

  &__ribbon-top {
    position: absolute;
    z-index: 20;

    @include md-down {
      top: -5.6650%;
      left: 52%;
      height: 22.9064%;
      transform-origin: left top;
      transform: rotate(17deg);
    }

    @include md-up {
      top: -7.5925%;
      right: -16.7708%;
      height: 37.5925%;
    }
  }

  &__ribbon-bottom {
    position: absolute;
    z-index: 20;

    @include md-down {
      right: -36.2666%;
      bottom: -5.7881%;
      height: 31.527%;
      transform-origin: left center;
      transform: rotate(-4deg);
    }

    @include md-up {
      right: -10.0520%;
      bottom: -13.8888%;
      height: 56.4814%;
    }
  }

  &__person {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    height: 100%;
  }

  &__body {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;

    @include md-down {
      left: 0;
    }

    @include md-up {
      left: 50%;
      transform: translateX(-50%);
      display: flex;
      justify-content: center;
    }
  }

  .dead-body {
    &__image {
      width: auto;
      height: 100%;

      @include md-down {
        position: absolute;
        top: 0;
        right: 0;
        width: auto;
        height: 100%;
      }
    }
  }

  .social {
    &__list {
      position: absolute;
      top: 50%;
      z-index: 40;
      display: flex;

      @include sm-down {
        right: 13vw;
        margin-top: 10%;
      }

      @include iphone-x  {
        right: 20vw;
      }

      @include sm-up {
        right: 21vw;
        margin-top: 9%;
      }

      @include md-up {
        left: 50%;
        transform: translateX(-50%);
        margin-top: 15vh;
        margin-left: 20vh;
      }

      @include lowest-viewport {
        right: 26px;
        margin-top: 26px;
      }
    }

    &__item {
      font-size: 0;
    }

    &__item:not(:first-child) {
      margin-left: 3.8vw;

      @include sm-up {
        margin-left: 3.125vw;
      }

      @include md-up {
        margin-left: 5.556vh;
      }
    }

    &__link {
      transition: transform 0.2s ease-in-out;

      &:hover {
        transform: scale(1.1);
      }
    }

    &__icon {
      width: 8vw;

      @include sm-up {
        width: 5vw;
      }

      @include md-up {
        width: 5.926vh;
      }

      @include lowest-viewport {
        width: 30px;
      }
    }
  }
}